import React from "react";
import ShiftTableRow from "./ShiftTableRow";
import { useSelector } from "react-redux";
import EmployeeInfoCard from "./EmployeeInfoCard";
import EmployeeShiftNameandTime from "./EmployeeShiftNameandTime";

const ShiftTableBody = () => {
  const { weekFirstDate, weekEndDate,shiftId } = useSelector(
    (state) => state.shiftCalanderHeader
  );
 
  const data = null;
  const isLoading = false;
  const isSuccess = false;
  const isError = false;
  

  return (
    <tbody>
      {/* <ShiftTableRow /> */}
      {isLoading ? (
        <tr>
          <td>Loading....</td>
        </tr>
      ) : isError ? (
        <tr>
          <td>Something went wrong....</td>
        </tr>
      ) : (
        data?.data.getShiftdata.slice(0,10).map((userShiftData) => (
          <tr key={userShiftData?.id}>
            <EmployeeInfoCard userShiftData={userShiftData} />
            {userShiftData?.shiftdata.map((shiftTime) => (
              <EmployeeShiftNameandTime
                key={shiftTime?.date}
                shiftTime={shiftTime}
              />
            ))}
          </tr>
        ))
      )}
    </tbody>
  );
};

export default ShiftTableBody;
