import React from "react";

const ShiftTableHeader = () => {
  return (
    <thead>
      <tr>
        <th className="searchTh">
          <div className="searchByName">
            <input
              type="text"
              className="form-control"
              placeholder="Search Employee by Name"
            />
            <button type="button" className="nameSearchBtn">
              <img src="images/search-box.svg" alt="" width={14} />
            </button>
          </div>
        </th>
        <th>
          SUN <b>05</b>
        </th>
        <th>
          MON <b>06</b>
        </th>
        <th>
          TUE <b>07</b>
        </th>
        <th>
          WED <b>08</b>
        </th>
        <th>
          THU <b>09</b>
        </th>
        <th>
          FRI <b>10</b>
        </th>
        <th>
          SAT <b>11</b>
        </th>
      </tr>
    </thead>
  );
};

export default ShiftTableHeader;
