import React from "react";
import { useDispatch,useSelector } from "react-redux";
import { months } from "../../utils/Constants.config";

const ShiftCalanderHeader = () => {
  const{date,shiftCalanderMonth,shiftCalanderYear,weekFirstDate,weekEndDate}=useSelector((state)=>state.shiftCalanderHeader)

  const dispatch=useDispatch()
  const getNextWeek = (state) => {
    const nextweekDate = new Date(state.date.setDate(state.date.getDate() + 7));
    state.date = nextweekDate;
    state.shiftCalanderMonth = months[nextweekDate.getMonth()];
    state.shiftCalanderYear = nextweekDate.getFullYear();
    state.weekFirstDate = new Date(state.weekFirstDate.setDate(
      state.weekFirstDate.getDate() + 7
    ));
    state.weekEndDate = new Date(state.weekEndDate.setDate(
      state.weekEndDate.getDate() + 7
    ));
  };

  const getPreviousWeek = (state) => {
    const preweekDate = new Date(state.date.setDate(state.date.getDate() - 7));
    state.date = preweekDate;
    state.shiftCalanderMonth = months[preweekDate.getMonth()];
    state.shiftCalanderYear = preweekDate.getFullYear();
    state.weekFirstDate = new Date(state.weekFirstDate.setDate(
      state.weekFirstDate.getDate() - 7
    ));
    state.weekEndDate = new Date(state.weekEndDate.setDate(
      state.weekEndDate.getDate() - 7
    ));
  };
  
  return (
    <div className="d-flex align-items-center p-3">
      <h3 className="me-auto mb-0 fs-5 fw-semibold">{`${shiftCalanderMonth} ${shiftCalanderYear}`}</h3>
      <div className="customCalanderNav me-auto d-flex align-items-center">
       
          <img src="images/prev-icon.svg" style={{cursor:"pointer"}} alt=""  onClick={()=>dispatch(getPreviousWeek())}/>
       
        <span className="mx-3 d-block fs-7 fw-semibold mt-1">
          {/* 05 APR 2023 - 11 APR 2023 */}
          {`${weekFirstDate.getDate()} ${months[weekFirstDate.getMonth()]} ${weekFirstDate.getFullYear()} - ${weekEndDate.getDate()} ${months[weekEndDate.getMonth()]} ${weekEndDate.getFullYear()}`}
        </span>
       
          <img src="images/next-icon.svg" style={{cursor:"pointer"}}  alt=""  onClick={()=>dispatch(getNextWeek())}/>
      
      </div>
      <div className="dropdown moredwn">
        <button
          className="btn btn-primary addShiftBtn me-2 "
          type="button"
          data-bs-toggle="dropdown"
          aria-expanded="false"
        >
          <i className="bi bi-three-dots-vertical" /> More
        </button>
        <ul className="dropdown-menu ">
          <li>
            <a
              className="dropdown-item border-bottom"
              href="#addWeekOffshift"
              data-bs-toggle="modal"
            >
              Add Week Off
            </a>
          </li>
          <li>
            <a
              className="dropdown-item border-bottom"
              href="#teamMemberModaladd"
              data-bs-toggle="modal"
            >
              Add
            </a>
          </li>
          <li>
            <a
              className="dropdown-item"
              href="#shiftmanagementcanvas"
              data-bs-toggle="offcanvas"
            >
              Shift Configuration
            </a>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default ShiftCalanderHeader;
