import React from 'react'
import { Link } from 'react-router-dom'
import moment from 'moment-timezone'

const EmployeeInfoCard = ({userShiftData}) => {
  const firstDateOfMonth=new Date(new Date().getFullYear(),new Date().getMonth(),1)
  const lastDateOfMonth=new Date(new Date().getFullYear(),new Date().getMonth()+1,0)

  console.log("hellllllleeee",userShiftData)
  return (
    
      <td>
        <Link to={`/shift-management/${userShiftData?.id}/${moment(firstDateOfMonth).format("YYYY-MM-DD")}/${moment(lastDateOfMonth).format("YYYY-MM-DD")}`}>
      <div className="d-flex justify-content-between align-items-center">
        <a href="shift-view.html" className="userName">
          <figure>
            <img src="images/profile-img.png" alt="" />
          </figure>
          <p>
            <strong>{userShiftData?.first_name}</strong>
            {userShiftData?.Designation}
          </p>
        </a>
        <div className="deleteBG">
          <img src="images/delete-icon.svg" alt="" width={16} height={20} />
        </div>
      </div>
      </Link>
    </td>
 
    
  )
}

export default EmployeeInfoCard