import React from 'react'
import ShiftMenuDropdown from './ShiftMenuDropdown'
import {useDispatch} from 'react-redux'


const ShiftInfoCard = ({card ,index}) => {
  const dispatch=useDispatch()
  return (
      <li key={card?.id} className="nav-item" style={{backgroundColor:"#FFFFFF30",marginRight:"10px"}}>
      <a href="#shiftTab1" data-bs-toggle="tab" className={index==0?"nav-link active w-100":"nav-link w-100"}>
        <span>{card?.shift_name}</span>
        <strong>{`${card?.shift_start_time } ${card?.shift_end_time}`}</strong>
      </a>
      <ShiftMenuDropdown/>

   
    </li>
  )
}

export default ShiftInfoCard